<template>
  <div class="course-broadcast">
    <head-top title="屏幕广播">
      <span slot="right" class="head-right" @click="toBroadcast">
       <span class="head_span">{{broadcastIP ? '停止广播' : '开始广播'}} </span>
      </span>
    </head-top>

<!--    <div class="sub_nav">-->
<!--      <span>老师屏幕</span>-->
<!--    </div>-->
    <van-radio-group v-model="teachScreenSelected" style="text-align: left;" @change="changeTeachScreen"
                     :disabled="broadcastIP.length>0">
      <van-cell-group>
        <van-cell v-for="(item, index) in options" :key="index" :title="item.text" clickable
                  @click="teachScreenSelect(item.value)">
          <van-radio slot="right-icon" :name="item.value"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>

    <template v-if="gatewayList.length>0">
      <div class="sub_nav">
        <span>小组屏幕</span>
      </div>
      <van-radio-group v-model="gatewayScreenSelected" style="text-align: left;" @change="changeGatewayScreen"
                       :disabled="broadcastIP.length>0" v-if="gatewayList && gatewayList.length>0">
        <van-cell-group>
          <van-cell v-for="(item, index) in gatewayList" :key="index" :title="item.text" clickable
                    @click="gatewayScreenSelect(item.value)">
            <van-radio slot="right-icon" :name="item.value"></van-radio>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
      <r-lack-data v-else></r-lack-data>
    </template>
  </div>
</template>

<script>
import { Dialog } from "vant";

export default {
  name: "broadcast",
  data () {
    return {
      broadcastIP: "",
      options: [
        { text: "主屏", value: "123" },
        { text: "副屏", value: "123456" }
      ],
      teachScreenSelected: "",
      gatewayList: [],
      gatewayScreenSelected: ""
    };
  },
  created () {
    if (this.broadcastIP) {
      const teachIndex = this.options.findIndex(r => r.value === this.broadcastIP);
      const gatewayIndex = this.gatewayList.findIndex(r => r.value === this.broadcastIP);
      if (teachIndex > -1) {
        this.teachScreenSelected = this.broadcastIP;
      } else if (gatewayIndex > -1) {
        this.gatewayScreenSelected = this.broadcastIP;
      }
    }
    this.getDataInfo();
    // this.getGatewayList();
  },
  methods: {
    getDataInfo () {
      this.$gatewayHttp.broadcast.getDataInfo().then(res => {
        if (res.Result) {
          this.broadcastIP = res["ID"];
          if (this.options.find(r => r.value === res["ID"])) {
            this.teachScreenSelected = res["ID"];
            this.gatewayScreenSelected = "";
          } else {
            this.teachScreenSelected = "";
            this.gatewayScreenSelected = res["ID"];
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getGatewayList () {
      this.gatewayList = [];
      this.$gatewayHttp.broadcast.getGatewayList().then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.gatewayList.push({
                text: item["GatewayName"],
                value: item["GatewayID"],
                IP: item["GatewayIP"],
                state: item["State"],
                online: item["IsOnline"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    teachScreenSelect (value) {
      if (this.broadcastIP.length === 0) this.teachScreenSelected = value;
    },
    changeTeachScreen (value) {
      if (value) {
        this.teachScreenSelected = value;
        this.gatewayScreenSelected = "";
      }
    },
    gatewayScreenSelect (value) {
      if (this.broadcastIP.length === 0) this.gatewayScreenSelected = value;
    },
    changeGatewayScreen (value) {
      if (value) {
        this.gatewayScreenSelected = value;
        this.teachScreenSelected = "";
      }
    },
    toBroadcast () {
      if (this.broadcastIP) {
        Dialog.confirm({
          message: "确认停止广播？"
        }).then(() => {
          this.stopBroadcast();
        }).catch(() => {
        });
      } else {
        Dialog.confirm({
          message: "确认开始广播？"
        }).then(() => {
          this.startBroadcast();
        }).catch(() => {
        });
      }
    },
    startBroadcast () {
      const id = this.teachScreenSelected || this.gatewayScreenSelected;
      this.$gatewayHttp.broadcast.setStart({
        id
      }).then(res => {
        if (res.Result) {
          this.broadcastIP = id;
          this.$store.commit("SET_BROADCAST_IP", this.broadcastIP);
          this.$toast("成功发送开始广播");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("发送开始广播失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    stopBroadcast () {
      this.$gatewayHttp.broadcast.setStop({
        id: this.broadcastIP
      }).then(res => {
        if (res.Result) {
          this.gatewayScreenSelected = "";
          this.teachScreenSelected = "";
          this.broadcastIP = "";
          this.$store.commit("SET_BROADCAST_IP", "");
          this.$toast("成功发送停止广播");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("发送停止广播失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-broadcast {
  background-color: #fff;
  min-height: 100%;

}
</style>
